
#long_quiz_box{
    width: 100%;
    height: 300px;
    margin-top: 100px;

    #answer, #answer2{
        width: 90%;
        height: 50px;
        font-size: larger;
        text-align:center;
    }
    #menu_btn{
        height: 40px;
        width: 80px;
        margin: 20px auto;
        display: block;
    }
 #restart_btn{
